body {
  color: #FFF;
  background-color: #01082d;
  background: #e9275b;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#e9275b), to(#ffff66));
  background: linear-gradient(to bottom, #e9275b 0%, #ffff66 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e9275b', endColorstr='#ffff66',GradientType=0 );
  /* IE6-9 */
  font-size: 14px;
  position: relative;
  font-family: "Montserrat", sans-serif;
  margin: 0;
  width: 100%;
  background-attachment: fixed; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.page {
  position: relative; }
  .page .page-description {
    max-width: 450px;
    margin: 0 auto;
    background: #392270;
    border: 3px solid #5736A7;
    font-family: 'Open Sans', sans-serif;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 25px; }

.page-content {
  min-height: calc(100vh - 123px);
  background: #01082d;
  margin-top: -2px;
  padding-top: 100px;
  z-index: 100; }
  .page-content .content-wrapper {
    max-width: 1000px;
    margin: 0 auto; }
  @media screen and (max-width: 800px) {
    .page-content {
      padding: 0px;
      padding-top: 50px;
      width: 100vw; } }

.logo-bg {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: -1; }
  .logo-bg .header-logo {
    margin-top: 30vh;
    max-width: 275px;
    width: 25%;
    height: auto;
    min-width: 200px; }

